body {
    margin: 0;
    font-family: "Alegreya Sans", sans-serif;
    display: flex;
    flex-direction: column;
    min-height: 100vh; 
  }
  
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    padding: 10px;
    color: #FFFFFF;
    margin-bottom: 0;
  }
  .header-link {
    display: flex; 
    align-items: center; 
    text-decoration: none; 
    color: inherit; 
}
  
  .logo {
    img {
      height: 100px;
      width: 100px;
      transition: transform 0.3s ease;
    }
  }
  
    .header-link:hover .logo img {
    transform: scale(1.1); 
    }


  
  .title {
    display: inline-block;
    vertical-align: middle;
    font-family: 'Familjen Grotesk', sans-serif;
    margin-right: 45%;
  }
  
  .main-title {
    color: rgb(123, 26, 26);
    margin: 0;
    font-size: 1.2em;
    white-space: nowrap;
  }
  
  .sub-title {
    color: beige;
    margin: 0;
    font-size: 1.1em;
  }
  
  .menu {
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      flex-wrap: wrap;
      font-size: 20px;
      margin-right: 30px;
  
      li {
        margin-left: 30px;
  
        a {
          text-decoration: none;
          color: white;
          font-weight: bold;
  
          &:hover {
            color: rgb(123, 26, 26);
          }
        }
      }
    }
  }
  
  main {
    flex: 1; 
    display: flex;
    justify-content: center;
    align-items: center;
    
    .imagen-container {
      position: relative;
      width: 100%;
      margin-bottom: -5px;
  
      .imagen {
        width: 100%; 
        height: 100%; 
        object-fit: cover;
        opacity: 0.7;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
      }
  
      .texto-overlay {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: rgb(123, 26, 26);
        font-size: 4em;
        font-weight: 100;
        text-align: center;
        white-space: nowrap;
        font-family: 'Familjen Grotesk', sans-serif;

      }
    }
  
    .blanc-text {
      color: black;
      font-family: 'Familjen Grotesk', sans-serif;
    }
  }
  
  footer {
    text-align: center;
    padding: 10px;
    background-color:black;
    color: white;
  }
  
  /*Media Queries para Responsividad*/
  @media (max-width: 768px) {
    header {
      flex-direction: column;
      align-items: center;
      padding: 10px;
    }
  
    .menu {
      ul {
        flex-direction: column;
        align-items: flex-end;
      }
  
      li {
        margin: 5px 0;
      }
    }
  
    .imagen-container {
      .texto-overlay {
        font-size: 1.5em;
      }
    }
  }
  
  @media (max-width: 480px) {
    .imagen-container {
      .texto-overlay {
        font-size: 1.2em;
      }
    }
  }